import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import { withRouter } from 'react-router'
import * as actions from '../actions'
import { getVisibleTodos, getIsFetching, getErrorMessage } from '../reducers'
import TodoList from './TodoList'
import FetchError from './FetchError'

// No pode criar stateless, e.g. VisibleTodoList({ propA, propB })
// Aqui e importante ter contexto. Portanto os parametros sao 'passados'
// via connect()
class VisibleTodoList extends Component {
  componentDidMount() {
    this.fetchData()
  }

  componentDidUpdate(prevProps) {
    if (this.props.filter !== prevProps.filter) {
      this.fetchData()
    }
  }

  fetchData() {
    const { filter, fetchTodos } = this.props
    fetchTodos(filter)
  }
  /**
    Conveno de nomes de callbacks:

    Todo            => <li onClick=onClick          => onClick, generico
    TodoList        => <Todo onClick=onTodoClick    => onTodoClick, generico mas pertence ao comp Todo
    VisibleTodoList => <TodoList onClick=toggleTodo => toggleTodo, ao do evento
   */
  render() {
    const { toggleTodo, todos, isFetching, errorMessage } = this.props

    if (isFetching && !todos.length) {
      return <p>Loading...</p>
    }

    if (errorMessage && !todos.length) {
      return (
        <FetchError message={errorMessage} onRetry={() => this.fetchData()} />
      )
    }

    return <TodoList todos={todos} onTodoClick={toggleTodo} />
  }
}
VisibleTodoList.propTypes = {
  filter: PropTypes.string.isRequired,
  toggleTodo: PropTypes.func.isRequired,
  isFetching: PropTypes.bool.isRequired,
  errorMessage: PropTypes.string,
  todos: PropTypes.arrayOf(PropTypes.object).isRequired,
  fetchTodos: PropTypes.func.isRequired
}
VisibleTodoList.defaultProps = {
  errorMessage: ''
}

const mapStateToProps = (state, { match: { params } }) => {
  
  const filter = getFilterHelper(params)

  return {
    todos: getVisibleTodos(state, filter),
    isFetching: getIsFetching(state, filter),
    errorMessage: getErrorMessage(state, filter),
    filter
  }
}

// Lembrar que nao modifica o componente passado (VisibleTodoList, no caso)
// mas sim, retorna um novo componente (connected component).
// Este novo componente tera seus atributos atualizados, com o valor de state.
export default withRouter(connect(mapStateToProps, actions)(VisibleTodoList))

// Helpers -------------------------------
const getFilterHelper = (params) => {
  let filter = ''
    if (!params.filter === undefined) {
      filter = 'all'
    } else if (
      params.filter !== 'all' &&
      params.filter !== 'active' &&
      params.filter !== 'completed'
    ) {
      filter = 'all'
    } else {
      filter = params.filter
    }
  return filter
}
