const byId = (state = {}, action) => {
  if (action.response) {
    return {
      ...state,
      ...action.response.entities.todos
    }
  }
  return state
}

export default byId

// State aqui  2 level. E.g todoState.byId
export const getTodo = (state, id) => state[id]



